import React from 'react';
import { Link } from 'gatsby';
import styled from '@emotion/styled';
import Headroom from 'react-headroom';
import logo from '../../static/logo/header-logo.png';
import ForumI from '../../static/logo/forum-icon.png';

const StyledLink = styled(Link)`
  display: flex;
  font-weight: 700;
  align-items: center;
  height: 2vh;
  width: 3vh;
  padding-top: 4vh;
`;

const Nav = styled.nav`
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  font-family: ${props => props.theme.fontFamily.body2};
  font-weight: 300;
  font-size: 0.8rem;

  align-items: center;
  a {
    text-transform: uppercase;
  background: linear-gradient(to right, rgba(56,30,201,1) 0%, rgba(208,66,255,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #183D58;
    margin-left: 2rem;
    transition: all ${props => props.theme.transitions.default.duration};
    &:hover {
      animation: text-shadow 1.5s ease-in-out infinite;
    font-size: 1em;
    font-weight: 900;

    }
  }
`;

const NavBar = () => (
  <Headroom calcHeightOnResize disableInlineStyles>
    <StyledLink to="/">
      <img src={logo} alt="Limitless Logo" />
      <img src={ForumI} alt="Unity Forum" />
    </StyledLink>
    <StyledLink to="/">
      
    </StyledLink>
    <Nav id = "navvB">
      <Link to="/">Home</Link>
      <Link to="/blog">Blog</Link>
      <Link to="/about">About</Link>
    </Nav>
  </Headroom>
);

export default NavBar;
